Template.__checkName("lastRsvp");
Template["lastRsvp"] = new Template("Template.lastRsvp", (function() {
  var view = this;
  return Blaze.Each(function() {
    return Spacebars.call(view.lookup("rsvps"));
  }, function() {
    return [ "\n    ", Blaze.View("lookup:name", function() {
      return Spacebars.mustache(view.lookup("name"));
    }), " ", Blaze.View("lookup:last", function() {
      return Spacebars.mustache(view.lookup("last"));
    }), "\n  " ];
  });
}));
