
Template.__checkName("loginLayout");
Template["loginLayout"] = new Template("Template.loginLayout", (function() {
  var view = this;
  return [ Spacebars.include(view.lookupTemplate("fixedHeader")), "\n\n  ", Blaze.If(function() {
    return Spacebars.call(view.lookup("currentUser"));
  }, function() {
    return [ "\n        ", Spacebars.include(view.lookupTemplate("application")), "\n\n    " ];
  }, function() {
    return [ "\n        ", Spacebars.include(view.lookupTemplate("login")), "\n         \n    " ];
  }) ];
}));

Template.__checkName("registerTemplate");
Template["registerTemplate"] = new Template("Template.registerTemplate", (function() {
  var view = this;
  return Spacebars.include(view.lookupTemplate("yield"));
}));
